// Настройки ===============================================================================================================================================================================================================================================================================================
@import "_base/settings";
@import "_base/components";
@import "_base/components/items";


.header {

	&__row1 {
		border-bottom: 1px solid #EAEAEA;

		.container {
			display: flex;
			align-items: center;
			gap: 24px;
			min-height: 72px;
			font-weight: 500;
			ul {
				display: flex;
				gap: 24px;
				height: 100%;
				a {
					line-height: 20px;
					position: relative;
					&::before {
					    position: absolute;
					    bottom: 0;
						left: 0;
						width: 100%;
						height: 1px;
						background-color: #000;
						content: '';
						transition: all .3s;
					}

					&:hover {
						&::before {
							width: 0;
						}
					}

				}
			}
			.email {
				margin-left: auto;
				position: relative;

				&::before {
					position: absolute;
					bottom: 0;
					left: 0;
					width: 100%;
					height: 1px;
					background-color: #000;
					content: '';
					transition: all .3s;
				}
				&:hover {
					&::before {
						width: 0;
					}
				}

			}

			.sohial {
				display: flex;
				gap: 8px;
				a {
					height: 26px;
					width: 26px;
				}
				&__item--tw {
					background: url("../img/icon/sohial/tw.svg");
				}
				&__item--whatsap {
					background: url("../img/icon/sohial/whatsap.svg");
				}
				&__item--viber {
					background: url("../img/icon/sohial/viber.svg");
				}
			}

			.btn {
				border-radius: 20px;
				display: flex;
				align-items: center;
				gap: 12px;
				padding: 15px;
				&::before {
					display: block;
					min-width: 12px;
					min-height: 12px;
					background: url("../img/icon/phone.svg");
					content: '';
				}
			}

			.phone {
				font-size: 18px;
				font-weight: 600;
			}

		}
	}

	&__row2 {
		border-bottom: 1px solid #EAEAEA;
		.container {
			display: flex;
			align-items: center;
			min-height: 90px;

		}
		.logo {
			max-width: 151px;
			margin-right: 44px;
		}

		&-slogan {
			max-width: 117px;
			font-size: 14px;
			letter-spacing: -0.5px;
			color: #5D5965;
		}


		&-search {
			border-left: 1px solid #EAEAEA;
			border-right: 1px solid #EAEAEA;
			align-self: stretch;
			margin-left: 78px;
			display: flex;
			flex-grow: 1;
			max-width: 581px;
			align-items: center;

			.btn {
				margin-left: 24px;
				background-color: transparent;
				padding: 0;
				&::before {
					display: block;
					min-width: 16px;
					min-height: 17px;
					background: url("../img/icon/search.svg");
					content: '';
				}
			}
			.placeholder-container {
				height: 40px;
				width: 100%;
				color: #CCC;
				position: relative;
				display: flex;
				input {
					border: none;

				}
			}
		}

		&-basket {
			display: flex;
			align-items: center;
			gap: 16px;
			margin-left: 42px;
			color: #BDBDBD;
			position: relative;
			&::after {
				min-width: 28px;
				min-height: 28px;
				background: url("../img/icon/basket.svg");
				content: '';
			}

			span {
				background-color: $accent;
				border-radius: 100%;
				min-width: 15px;
				min-height: 15px;
				font-weight: 700;
				font-size: 10px;
				display: flex;
				align-items: center;
				justify-content: center;
				position: absolute;
				top: -5px;
				right: -4px;
			}

		}


	}


}

.footer {
	padding: 35px 0 45px;
	background: #484848;
	font-weight: 400;
	font-size: 14px;
	line-height: lh(14, 30);
	color: #EAEAEA;
	&-row1 {
		display: flex;
		justify-content: space-between;
		h3 {
			font-weight: 500;
			font-size: 16px;
			line-height: lh(16, 22);
			margin-bottom: 12px;
		}
	}
}

.page-home {
	.offer {
		background: url("../img/1-screen-bg .jpg") center center/cover no-repeat;
		height: 768px;
	}
}











